import "./App.css";

import image from "./img/image_web.jpeg";

function App() {
  const notificationPermission = async () => {
    if (!("Notification" in window)) {
      console.log("Esse browser não suporta notificações desktop");
    } else {
      if (Notification.permission !== "denied") {
        await Notification.requestPermission();
      }
    }

    if (Notification.permission === "granted") {
      const notification = new Notification("NextTraining", {
        body: "NÃO ADIANTA NEM TENTAR!!!O PRETO JÁ É O CAMPEÃO!!!!",
      });
      notification.onClick = (e) => {
        e.preventDefault();
        window.focus();
        notification.close();
      };
    }
  };

  const handleClick = () => {
    let count = 1000;
    for (let i = 0; i < count; i++) {
      notificationPermission();
    }
  };

  return (
    <div className="App">
      <div className="title"> NOTÍCIA URGENTE </div>
      <p className="text">
        Devido aos problemas envolvendo a frente fria recente, alguns
        colaboradores precisaram desistir da viagem, com isso fomos forçados a
        fazer uma reorganização nos grupos, portanto pedimos que confirme se o
        seu nome continua no mesmo grupo, ou se foi alterado, caso tenha sido
        alterado , favor comunicar ao líder do grupo.
      </p>
      <div className="banner">
        <img src={image} alt="img" />
      </div>

      <div className="btn-link">
        <button onClick={handleClick}>VER GRUPOS </button>
      </div>
    </div>
  );
}

export default App;
